html {
    scroll-behavior: smooth;
  }
  
*::-webkit-scrollbar {
        display: none;
}

.home_page {
    width: 100vw;
    height: 100vh;
    scroll-snap-align: center;
}
.home_page_insider {
    width: 100%;
    height: 100%;
    display: flex;
    
}
.home_page_insider_left {
    width: 50%;
    height: 100%;
    position: relative;
    align-items: center;
    justify-content: center;
    background: #654ea3; /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #654ea3, #eaafc8); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #b30beb, #6a56eb);
    z-index: -1;
}
.home_page_insider_right {
    width: 50%;
    height: 100%;
    position: relative;
    z-index: -1;
    background-color: #000000;
}






.xcard_center_img{
    width: 100%;
    height: 100%  ;
    border-radius: 50%;
    background-color: green;
    border: 5px solid white ;
    pointer-events: none ;
}
.xcard_center_img_tag {
    pointer-events: none ;
    position: relative;
    border-radius: 50%;
    width: 200px;
    height: 200px  ;
    box-shadow: 0 0 10px rgba(52, 111, 238 , 0.3) , 0 0 40px rgba(52, 105, 238 , 0.3) , 0 0 80px rgba(52, 105, 238 , 0.3) ;
background: linear-gradient(90deg, rgb(80, 102, 228) 0%, rgb(48, 94, 223) 100%);
  border: none;
  transition: all 0.3s ease-in-out 0s;
  cursor: pointer;
  outline: none;
  overflow: visible;
  position: relative;
  top : 50% ; 
  left : 50% ; 
  transform-origin: center;
  transform: translate(-50% , -50%);
  animation: logoboom 3s ease-out infinite;
}

@keyframes logoboom {
    0% {
        transform  : scale(1) translate(-50% , -50%) ;
    }
    50% {
        transform  : scale(1.1) translate(-50% , -50%) ;
    }
    100% {
        transform  : scale(1) translate(-50% , -50%) ;
    }
}
.xcard_center_img_tag::after {
    content: '';
    width: 250px; 
    height:250px;
    border-radius: 100%;
    border: 6px solid rgb(137, 15, 237);
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: ring 3s infinite;
  }
  .xcard_center_img_tag::before {
      content: '';
      border-radius: 1000px;
      min-width: calc(300px + 12px);
      min-height: calc(60px + 12px);
      border: 6px solid #00FFCB;
      box-shadow: 0 0 60px rgba(0,255,203,.64);
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      opacity: 0;
      transition: all .3s ease-in-out 0s;
    }






  @keyframes ring {
    0% {
      width:30px;
      height:30px;
      opacity: 1;
    }
    100% {
      width: 700px;
      height: 700px;
      opacity: 0;
    }
  }













