.home_page_insider_left_insider {
    width: 80%;
    height: 50%;
    position: relative;
    top : 50% ;
    left : 50% ; 
    transform: translate(-50% , -50%);
    text-align: center;
    justify-content: center;
    align-items: center;
}
.home_page_insider_left_insider_mov{
    text-align: center;
    position: relative ;
    justify-content: center ;
    align-items: center ;
}
@import url('https://fonts.googleapis.com/css2?family=Bungee&family=Fugaz+One&display=swap');
.home_page_insider_left_insider_mov > h2:nth-of-type(1) {
    font-size: 50px;
    color: rgb(255, 255, 255) ;
    animation: fadein 6s ease-out infinite;
    font-family: 'Bungee', cursive;
    position: absolute;
    text-align: center;

}
.home_page_insider_left_insider_mov > h2:nth-of-type(2) {
    font-size: 50px;
    color: rgb(255, 255, 255) ;
    animation: fadein_2 6s ease-out infinite;
    font-family: 'Bungee', cursive;
    position: absolute;
    text-align: center;
    padding-left: 50px;
}

.home_page_insider_left_insider > h6 {
    font-size: 30px;
    font-family: 'Fugaz One', cursive;
    color: white ;
}



@keyframes fadein {
    0% {
        transform: translateY(50px);
        opacity: 0;
   
    }
    25% {
        transform: translateY(0px);
        opacity: 1;
 
    }
    50% {
        transform: translateY(50px);
        opacity: 0;
     
    }
    100% {
        transform: translateY(50px);
        opacity: 0;
      
            
    }
}

@keyframes fadein_2 {
    0% {
        transform: translateY(50px);
        opacity: 0;
     
    }
    50% {
        transform: translateY(50px);
        opacity: 0;
   
    }
    75% {
        transform: translateY(0px);
        opacity: 1;
  
    }
    100% {
        transform: translateY(50px);
        opacity: 0;

    }
}
