.main_page_bottom {
    position: fixed;
    width: 100%;
    height: 50px;
    justify-content: center;
    align-items: center;
    bottom: 10px;
    background-color: none;
    pointer-events: unset;   
    z-index: 1;
}
.main_page_bottom_next {
    width: 50px;
    height: 50px;
    background-color: rgb(137, 15, 237) ; 
    border-radius: 50% ;
    position: absolute;
    right : 50% ;
    -webkit-transform: translate(50% ,50% );
            transform: translate(50% ,50% );
    cursor: pointer;
    -webkit-animation: boom_aboutus 1s ease-out infinite;
            animation: boom_aboutus 1s ease-out infinite;
    z-index: 99999;
    
}
@-webkit-keyframes boom_aboutus {
    0% {
        -webkit-transform: scale(0)  translate(50% , 50% );
                transform: scale(0)  translate(50% , 50% );
    }
    50% {
        -webkit-transform: scale(1)  translate(50% , 50%) ;
                transform: scale(1)  translate(50% , 50%) ;

    }
    100% {
        -webkit-transform: scale(0)  translate(50% , 50%) ;
                transform: scale(0)  translate(50% , 50%) ;
    }
}
@keyframes boom_aboutus {
    0% {
        -webkit-transform: scale(0)  translate(50% , 50% );
                transform: scale(0)  translate(50% , 50% );
    }
    50% {
        -webkit-transform: scale(1)  translate(50% , 50%) ;
                transform: scale(1)  translate(50% , 50%) ;

    }
    100% {
        -webkit-transform: scale(0)  translate(50% , 50%) ;
                transform: scale(0)  translate(50% , 50%) ;
    }
}
