.xcard_center_img_tag {
    pointer-events: none ;
    position: relative;
    border-radius: 50%;
    width: 200px;
    height: 200px  ;
    box-shadow: 0 0 10px rgba(52, 111, 238 , 0.3) , 0 0 40px rgba(52, 105, 238 , 0.3) , 0 0 80px rgba(52, 105, 238 , 0.3) ;
background: linear-gradient(90deg, rgb(80, 102, 228) 0%, rgb(48, 94, 223) 100%);
  border: none;
  transition: all 0.3s ease-in-out 0s;
  cursor: pointer;
  outline: none;
  overflow: visible;
  position: relative;
  top : 50% ; 
  left : 50% ; 
  -webkit-transform-origin: center; 
          transform-origin: center;
  -webkit-transform: translate(-50% , -50%);
          transform: translate(-50% , -50%);
  -webkit-animation: logoboom 3s ease-out infinite;
          animation: logoboom 3s ease-out infinite;
}

@-webkit-keyframes logoboom {
    0% {
        -webkit-transform  : scale(1) translate(-50% , -50%) ;
                transform  : scale(1) translate(-50% , -50%) ;
    }
    50% {
        -webkit-transform  : scale(1.1) translate(-50% , -50%) ;
                transform  : scale(1.1) translate(-50% , -50%) ;
    }
    100% {
        -webkit-transform  : scale(1) translate(-50% , -50%) ;
                transform  : scale(1) translate(-50% , -50%) ;
    }
}

@keyframes logoboom {
    0% {
        -webkit-transform  : scale(1) translate(-50% , -50%) ;
                transform  : scale(1) translate(-50% , -50%) ;
    }
    50% {
        -webkit-transform  : scale(1.1) translate(-50% , -50%) ;
                transform  : scale(1.1) translate(-50% , -50%) ;
    }
    100% {
        -webkit-transform  : scale(1) translate(-50% , -50%) ;
                transform  : scale(1) translate(-50% , -50%) ;
    }
}
.xcard_center_img_tag::after {
    content: '';
    width: 250px; 
    height:250px;
    border-radius: 100%;
    border: 6px solid rgb(137, 15, 237);
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-animation: ring 3s infinite;
            animation: ring 3s infinite;
  }
  .xcard_center_img_tag::before {
      content: '';
      border-radius: 1000px;
      min-width: calc(300px + 12px);
      min-height: calc(60px + 12px);
      border: 6px solid #00FFCB;
      box-shadow: 0 0 60px rgba(0,255,203,.64);
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      opacity: 0;
      transition: all .3s ease-in-out 0s;
    }






  @-webkit-keyframes ring {
    0% {
      width:30px;
      height:30px;
      opacity: 1;
    }
    100% {
      width: 700px;
      height: 700px;
      opacity: 0;
    }
  }






  @keyframes ring {
    0% {
      width:30px;
      height:30px;
      opacity: 1;
    }
    100% {
      width: 700px;
      height: 700px;
      opacity: 0;
    }
  }













