.projects_page {
    background-color: #000000;
    width: 100vw;
    height: 100vh;
    scroll-snap-align: start ;
    position: relative;
    z-index: 0;
}


.projects_page_insider {
    width: 90%;
    height: 90%;
    position: relative;
    left: 50%;
    top : 50% ; 
    transform: translate(-50% , -50%);
}
.projects_page_insider_carousel {
    width: 100%;
    height: 80% ; 
}


@import url('https://fonts.googleapis.com/css2?family=Bungee&family=Fugaz+One&display=swap');

.projects_page_insider_top_title {
    text-align: center;
    width: 100%;
    color: white;
    font-family: 'Bungee', cursive;
}




































.projectscontainer {
    width: 100%;
    height: 100%;
    color: cornsilk;
    border-radius: 50px;
    position: relative;
}

.projectscontaine_insider {
    width: 80%;
    height: 80%;

    display: flex;
    position: relative;
    top : 50% ; 
    left  : 50% ; 
    transform: translate(-50% , -50%);
}
.carousel-inner {
    width: 100%;
    height: 100%;
}

.carousel-item {
    width: 100%;
    height: 100%;
}





.projectscontainer_left {
    width: 50%;
    height: 100%;
    background-color: white;
    
}

.projectscontainer_left > img {
    height: 100%;
    width: 100%;
}




.projectscontainer_right {
    width: 50%;
    height: 100%;
    background-color: white ;
    border-bottom-right-radius: 80px;
    border-top-right-radius: 80px;
    padding-top: 20px;
    position: relative;
    text-align: center;
}




.projectscontainer_right > h1 {
    color : rgb(222, 56, 255) ; 
    font-family: 'Bungee', cursive;  
    width: 100%;
    text-align: center;
}

@import url('https://fonts.googleapis.com/css2?family=Quicksand&display=swap');

.projectscontainer_right > p {
    color : rgb(110, 109, 109) ; 
    font-family: 'Quicksand', sans-serif;
    width: 100%;
    text-align: center;
    padding: 20px ;
    padding-top: 0px;
}


.projectscontainer_right > h6 {
    color : white ; 
    padding: 5px ;
    background-color: red;
    border-radius: 10px;
    font-family: 'Quicksand', sans-serif;
    width: 100px;
    position: relative;
    left : 50% ;
    transform: translate(-50% , 0%) ;
    text-align: center;

}
.projectscontainer_right > a {
    color : rgb(0, 123, 255) !important ; 
    border-radius: 10px;
    font-family: 'Quicksand', sans-serif;
    position: relative;
    text-align: center;
    font-size: 20px;
    padding-top: 5px;
    width: 100%;
    text-decoration: none;
    font-weight: bolder;
    cursor: pointer;

}

.projectscontainer_right_love {
    position: absolute;
    bottom: 10px;
    width: 100%;
    height: 50px;
    overflow: visible;
}
.projectscontainer_right_love_icon {
    color :rgba(29, 29, 29, 0.207) ;
    font-size: 60px;
    text-align: center;
    font-weight: 500;
    cursor: pointer;
    overflow: visible;
}
.projectscontainer_right_love_icon_after {
    color : red ;
    font-size: 60px;
    text-align: center;
    font-weight: 500;
    cursor: pointer;
    overflow: visible;
}