.aboutme_page {
    background: linear-gradient(to right, #b30beb, #6a56eb);
    height: 100vh;
    width: 100vw;
    scroll-snap-align: start;
}
@import url('https://fonts.googleapis.com/css2?family=Bungee&family=Fugaz+One&display=swap');

.aboutme_page > h1 {
    color : white ; 
    font-family: 'Bungee', cursive;
    text-align: center;
    padding: 20px;
    width: 100%;
}




.aboutme_page_layout {
    width: 80%;
    height: 400px;
    position: relative;
    left : 50% ;
    transform: translate(-50%  , 0%) ;
    display: flex;
}

.aboutme_page_layout_left {
    width: 50%;
    height: 100%;
    background-color: black;
    background-image: url(..//media/profile/pj2.jpeg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}
@import url('https://fonts.googleapis.com/css2?family=Play&family=Quicksand&display=swap');
.aboutme_page_layout_right {
    width: 50%;
    height: 80%;
    position: relative;
    top : 50%  ;
    left : -100px ; 
    transform: translate(0% , -50%) ;
    background-color: white;
    box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12)
  ;
  text-align: center;
  color: gray;
  font-family: 'Quicksand', sans-serif;
  border-radius: 3px;

}
.aboutme_page_layout_right > h4 {
  text-align: left;
  color: gray;
  font-family: 'Quicksand', sans-serif;
  font-size: 25px;
  padding: 20px;
  padding-top: 0px;


}
.aboutme_page_layout_right > h6 {
    padding-top: 20px;
    text-align: center;
    color: blueviolet;
    font-family: 'Quicksand', sans-serif;
    font-size: 28px;
    font-weight: bolder;
  }